import { BASE_URL } from "../constants/unit";
import { getAuthToken } from "../utils";

export const getMe = () => {
  const token = getAuthToken();
  return fetch(`${BASE_URL}/users/me`, {
    headers: {
      "content-type": "application/json",
      authorization: `Bearer ${token}`,
    },
  }).then((res) => res.json());
};
